import styled from 'styled-components';

export const DetailWrapper = styled.div`
	overflow: hidden;
	width: 620px;
	margin: 0 auto;
	padding-bottom: 100px;
`;

export const Header = styled.div`
	margin: 50px 0 20px 0;
	line-height: 44px;
	font-size: 34px;
	color: #333;
	font-weight: bold;
`;

export const Content = styled.textarea.attrs({
  row: 4
})`
	color: ##2f2f2f;
  width: 90%;
  height: 400px;
`;